












































































































import uniNavBar from '../../components/uni-nav-bar.vue'
import uniIcon from '../../components/uni-icon.vue'
// import uniBadge from "../../components/uni-badge/uni-badge.vue"

export default {
	components: {
		uniNavBar,
		uniIcon,
		// uniBadge
	},
	data() {
		return {
			city: '',
			bannerDatas:{
				autoplay:true,
				indicatorDots:true,
				indicatorColor:'#CCCCCC',
				indicatorActivevColor:'#FFFFFF',
				previousMargin:20+'rpx',
				nextMargin:20+'rpx',
				circular:true,
				interval:5000
			},
			banner:[],
			user:{},
			card:[],
			tc:{},
			advert:{},
			goods:[],
			msg_num:''
		}
	},
	onLoad:function(){
		uni.showLoading({
			title: '加载中。。。',
			mask: false
		});
		var token=(uni.getStorageSync("user_info")).token;
		console.log(token);
		uni.request({
			url: 'http://bus.liebianzhe.com/api/user/index',
			method: 'POST',
			data: {
				token:token
			},
			success: res => {
				console.info(res.data);
				if(res.data.code==1){
					this.city=res.data.data.data.city;
					this.banner=res.data.data.data.banner;
					this.user=res.data.data.user_data;
					this.card=res.data.data.data.card;
					this.tc=res.data.data.data.tc;
					this.advert=res.data.data.data.advert;
					this.goods=res.data.data.data.goods;
					this.msg_num=res.data.data.data.new_msg;
					uni.hideLoading();
				}else if(res.data.code==10){
					uni.removeStorage({
						key:'user_info',
					})
					uni.showToast({
						title: res.data.msg,
						icon:'none',
						mask: false,
						duration: 1000
					});
					setTimeout(function(){
						uni.reLaunch({
							url:'../login/login'
						})
					},1500)
					
				}else{
					uni.showToast({
						title: res.data.msg,
						icon:'none',
						mask: false,
						duration: 1000
					});
				}
			},
			fail: () => {
				console.info("请求失败~");
			}
		});
	},
	methods: {
		jump_mes(){
			uni.navigateTo({
				url:'../messages/messages'
			})
		},		
		onPullDownRefresh() {
			console.log('onPullDownRefresh')
			setTimeout(function() {
				uni.stopPullDownRefresh()
				console.log('stopPullDownRefresh')
			}, 1000)
		},
		jump_hot(){
			uni.navigateTo({
				url:'../index/hot_adver'
			})
		},
		goods_details(id){
			uni.navigateTo({
				url: '../goods/goods-details/goods-details?id='+id,
			});
		}
	}
}
